/* General Page Styling */
.project-details-page {
  font-family: Arial, sans-serif;
  background-color: #f9f9f9;
  display: flex;
  flex-direction: column;
  align-items: center; 
  min-height: 100vh; 
  margin: 0;
  padding: 0;
}

/* Main Container */
.container {
  padding: 20px;
  max-width: 800px; 
  width: 100%; 
  margin: 6rem auto; 
  background: white;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

/* Title Styling */
.title {
  text-align: center;
  font-size: 32px;
  margin-bottom: 25px;
  color: #333;
}

/* Details Box */
.details-box {
  padding: 30px;
}

.details-box p {
  margin-bottom: 20px;
  font-size: 18px;
  line-height: 1.6;
  color: #555;
}

/* Form Styling */
.form-group {
  margin-bottom: 20px;
}

label {
  font-size: 18px;
  font-weight: bold;
  color: #333;
}

input, textarea {
  width: 100%;
  padding: 12px;
  margin-top: 8px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

textarea {
  resize: vertical;
  min-height: 100px;
}

/* Apply Button */
.apply-now-btn {
  display: block;
  margin: 30px auto; 
  padding: 12px 25px;
  background-color: #333;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 18px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.apply-now-btn:hover {
  background-color: #555;
}
